import React, { Component } from 'react';
import './Detail.scss';

class Detail extends Component {
    render() {
        const { location } = this.props
        return (
            <div className="Dwrap">
                <div className="Dbox">
                    {/* <img href="{location.state.poster}"></img> */}
                    <p className="Dtitle">{location.state.title}</p>
                    <p className="Dyear">{location.state.year}</p>
                    <p className="Dgenres">{location.state.genres}</p>
                    <p className="Dsummary">{location.state.summary}</p>
                </div>
            </div>
        );
    }
}

export default Detail;